import { AppbarContainer, AppShell } from "@app/ui";
import { nextPreload, useTranslation } from "@fanfanlo";
import { useRouter } from 'next/router';
import { useEffect } from 'react';
export default function BasicSettingsPage() {
  const { t } = useTranslation("homepage/pages/im/room/meeting/content");
  const router = useRouter();
  const { roomId, callId } = router.query;

  useEffect(() => {
    nextPreload(router, '/im');
  }, []);
  useEffect(() => {
    // 如果没有 roomId，重定向到聊天列表
    if (!roomId && router.isReady) {
      router.push('/im');
    }
  }, [roomId, router]);

  // 可选：调试参数
  // console.log('title=', t("content.title"), 'roomId=', roomId, 'callId=', callId);
  return (
    <AppShell>
      <AppbarContainer appbarProps={{ title: t("content.title"), back: true }}>
        
      </AppbarContainer>
    </AppShell>
  );
} 